.reservation-item {
  background: var(--color-box-base);
  border: 1px solid var(--color-line-in-white);
  border-radius: 0.8rem;
  margin-top: 1.4rem;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.reservation-item .text-information header {
  padding: 1.2rem 0rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 2.4rem;
}

.reservation-item img {
  align-self: center;
  width: 17rem;
  height: 17rem;
  right: 1rem;
  bottom: 1rem;
  border-radius: 10%;
}

.reservation-item .text-information header strong {
  font: 700 2rem Archivo;
  display: block;
  color: var(--color-text-title);
}

.reservation-item .text-information header span {
  font-size: 1.4rem;
  display: block;
  margin-top: 0;
}

.reservation-header {
  position: relative;
}

.delete-btn {
  position: absolute;
  top: 0;
  left: 0;
  border: none;
  background-color: red;
  padding: 0.1rem 0.5rem;
  color: white;
  cursor: pointer;
  margin: 0.5rem;
  border-radius: 50%;
  font-size: 1.5rem;
  font-weight: bold;
}

.reservation-item .text-information > p {
  padding: 0 2rem;
  font-size: 1.4rem;
  line-height: 1.8rem;
}

.reservation-item .text-information > p:last-child {
  margin-bottom: 1rem;
}

@media (max-width: 550px) {
  .reservation-item {
    flex-direction: column;
  }
}

@media (min-width: 550px) {
  .reservation-item img {
    width: 11.5rem;
    height: 11.5rem;
    margin-right: 20px;
  }
}

@media (min-width: 700px) {
  .reservation-item .text-information header,
  .reservation-item .text-information footer {
    padding: 1.2rem 2rem;
  }

  .reservation-item .text-information > p {
    padding: 0 4.4rem;
  }

  .reservation-item img {
    position: absolute;
    right: 1rem;
    top: 1rem;
    width: 11.5rem;
    height: 11.5rem;
    border-radius: 10%;
    margin: 0;
  }
}
